
.insertChatname {
    display: flex;
    flex-direction: column;
    padding: 6px;
    align-items: center;
    justify-content: center;
    margin-top: 150px;
    border-top: 2px solid #000000;
    border-bottom: 3px solid #000000;
    width: 90%;
    height: 30%;
    
    h2 {
      font-family: 'Tilt Prism', cursive;
      font-size: 28px;
      font-weight: bold;
      color: #000000;
      margin-bottom: 20px;
    }
    
    input {
      border: none;
      border-radius: 20px;
      font-size: 18px;
      height: 20%;  
      width: 100%;
      margin-bottom: 30px;
      text-align: center;
      text-transform: uppercase;
      
    }
    
    button {
      background-color: #1BBAF7;
      color: #FFFFFF;
      border: none;
      border-radius: 20px;
      width: 90%;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 21px;
      text-transform: uppercase;
      cursor: pointer;
      
      &:hover {
        background-color: #128CE3;
      }
      
      &:active {
        background-color: #0E6EB5;
      }
    }
  }


  @media only screen and (min-width: 1024px) {

    .insertChatname {        
        width: 50%;
        
        h2 {
          font-family: 'Tilt Prism', cursive;
          font-size: 28px;
          font-weight: bold;
          color: #000000;
          margin-bottom: 20px;
        }
 
  }
}