@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;1,400;1,700&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;

}

:root{
    --mainColor: #1BBAF7;
    --mainColorLigh: #798ace;
    --secondaryColor: #DB2B39;
    --textColor: #eee;
}

header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    padding: 0 2rem;
    background-color: var(--mainColor);
    color: var(--textColor);

    h3 a{
        font-size: 24px;
        cursor: pointer;
        text-decoration: none;
        color: var(--textColor);
    }

    nav {
        display: flex;
        flex-direction: row;
        align-items: center;
       justify-content: right;
        
        ul {
        list-style: none;
        display: flex;
        justify-content: center;

        a {
        margin: 0 2rem;
        color: var(--textColor);
        text-decoration: none;
        display: inline-block;

        a.active{
            background-color: aqua;
        }

        &:hover{
            color: var(--secondaryColor);
        }
        }}
}
    .nav-btn{
        padding: 5px;
        cursor: pointer;
        background: transparent;
        border: none;
        outline: none;
        color: var(--textColor);
        visibility: hidden;
        opacity: 0;
        font-size: 1.8rem;
    }
}



@media only screen and (max-width: 1024px){
    header .nav-btn{
        visibility: visible;
        opacity: 1;
    }
    header {
        display: flex;
        flex-direction: row;
        nav{
        position: fixed;
        top: 0;
        right: 0;
        height: 60%;
        width: 40%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-bottom-left-radius: 50px;
        gap: 1.5rem;
        background-color: var(--mainColor);
        transition: 1s;
        transform: translateY(-100vh);
        ul{
            display: flex;
            flex-direction: column;
            li{
                margin-bottom: 20px;
            }
            
    }}}
    header .responsive_nav{
        transform: none;
  
    }
    nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;

    }
    nav a{
        font-size: 16px;

    }
}